import React, { useEffect, useRef } from "react";
import CodeMirror from "@uiw/react-codemirror";
import "codemirror/lib/codemirror.css";
import "codemirror/mode/javascript/javascript";
import "codemirror/mode/htmlmixed/htmlmixed";
import "codemirror/theme/dracula.css";
import code from "./cvinfo";
import "./CV.scss"
const sites = [
	{
		url: "https://linkedin.com/in/paramirez2",
		label: `'<i class="fa fa-linkedin" style="color:white" aria-hidden="true"></i> Linkedin'`,
	},
	{
		url: "https://wa.me/573502593551",
		label: `'<i class="fa fa-whatsapp" style="color:#25D366" aria-hidden="true"></i> Whatsapp'`,
	},
	{
		url: "https://github.com/paramirez",
		label: `'<i class="fa fa-github" style="color:white"  aria-hidden="true"></i> Github'`,
	},
	{
		url: "https://join.skype.com/invite/OgUaufO89J6o",
		label: `'<i class="fa fa-skype" style="color:#34B7F1"  aria-hidden="true"></i> pablo.434'`,
	},
];

export default function CV() {
	const codemirrorRef = useRef(null);
	useEffect(() => {
		const els = [
			...codemirrorRef.current.editor.display.lineDiv.getElementsByClassName(
				"cm-string"
			),
		].filter((element) => {
			return element.innerHTML === "'WEB_SITE'";
		});
		els.forEach((el, i) => {
			el.innerHTML = `<a target="__blank" class="link_editor" href="${sites[i].url}">${sites[i].label}</a>`;
		});
	}, []);
	return (
		<CodeMirror
			ref={codemirrorRef}
			className="mirror"
			value={code}
			options={{
				theme: "dracula",
				mode: "javascript",
				viewportMargin: Infinity,
			}}
		/>
	);
}
