import React from "react";
import kiwi from "./kiwi.svg";
import "./Home.scss";

export default function Home({ content, showImage, setSection}) {
	return (
		<div className="Home">
			{showImage && <img src={kiwi} className="kiwi" alt="Kiwidev.co" />}
			<h1>{content}</h1>
			<div
				className="resume"
				style={{ display: showImage ? "block" : "none" }}
				onClick={() => setSection(2)}
			>
				See my resume
			</div>
		</div>
	);
}
