import React, { useState } from "react";
import "./App.scss";
import Home from "./views/Home";
import CV from "./views/CV";

function App() {
	const [section, setSection] = useState(1);
	function getSection() {
		switch (section) {
			case 1:
				return <Home showImage={true} content="KiwiDev" setSection={setSection} />;
			case 2:
				return <CV />;
			case 3:
				return <Home content="En construcción" />;
			case 4:
				return <Home content="En construcción" />;
			default:
				return;
		}
	}
	return (
		<div className="App">
			<div className="container">
				<div className="header">
					<div onClick={() => setSection(1)}>Home</div>
					<div onClick={() => setSection(2)}>CV</div>
					<div onClick={() => setSection(3)}>Projects</div>
					<div onClick={() => setSection(4)}>Contact</div>
				</div>
				<div className="sections">{getSection()}</div>
			</div>
		</div>
	);
}

export default App;
